.contacts {
  min-width: 320px;
  overflow: hidden;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  width: 100%;
  display: grid;
  grid-template-columns: 50% 50%;

  @include vp-1199 {
    grid-template-columns: 100%;
  }
}

.contacts__user {
  background-color: $color-sapphire;

  & .container {
    padding: 66px 106px;
    margin-left: 0;
    min-width: 50%;

    @include vp-1199 {
      padding: 56px 133px 68px;
    }

    @include vp-767 {
      padding: 60px 15px 70px;
    }

    & form {
      display: grid;
      width: 410px;
      grid-template-columns: 190px 190px;
      grid-template-rows: 1fr 1fr;
      gap: 30px;

      @include vp-1199 {
        grid-template-columns: 206px 206px;
        column-gap: 60px;
        row-gap: 30px;
      }

      @include vp-767 {
        grid-template-columns: 1fr;
        row-gap: 10px;
      }

      & label {
        padding: 0;
        margin: 0;
        align-self: end;
      }

      & input {
        background-color: transparent;
        color: $color-default-white;
        padding: 10px 0 2px;
        border: none;
        border-bottom: 1px solid $color-light-slate-grey;
        appearance: none;
        outline: none;
        width: 100%;
        height: 100%;
        position: relative;

        &:hover {
          border-bottom: 2px solid $color-default-white;
        }

        &:focus,
        &:active {
          text-indent: 10px;
          border: 2px solid $color-default-white;
        }

        &:disabled {
          color: $color-default-white;
          border-bottom: 1px solid $color-default-white;
          opacity: 0.4;
        }
      }

      & input::placeholder {
        color: $color-default-white;
        line-height: 1.5;

        @include vp-767 {
          font-size: 14px;
        }
      }

      & input:focus::placeholder,
      & input:active::placeholder {
        color: transparent;
      }

      & button {
        grid-column: 1/-1;
        background-color: transparent;
        color: $color-default-white;
        font-size: 14px;
        font-weight: 700;
        border: none;
        appearance: none;
        outline: 3px solid $color-default-white;
        text-transform: uppercase;
        min-height: 52px;
        cursor: pointer;

        @include vp-767 {
          margin-top: 24px;
        }

        @include hover-focus {
          background-color: $color-default-white;
          color: $color-neon-blue;
        }

        &:active {
          background-color: $color-default-white;
          color: $color-sapphire;
        }

        &:disabled {
          background-color: transparent;
          color: $color-default-white;
          outline: 3px solid $color-default-white;
          opacity: 0.3;
        }
      }
    }
  }

  h2 {
    color: $color-default-white;
    font-size: 54px;
    font-weight: 800;


    @include vp-1199 {
      font-size: 42px;
      font-weight: 700;
      margin-bottom: 12px;
    }

    @include vp-1199 {
      font-size: 32px;
    }
  }
}

.contacts__company {
  background-color: $color-default-white;
  border-top: 1px solid $color-light-slate-grey;

  & .container {
    flex-direction: column;
    padding: 50px 123px 68px;
    margin-left: 0;
    min-width: 50%;

    @include vp-1199 {
      padding: 60px 133px;
    }

    @include vp-767 {
      padding: 66px 15px 60px;
    }
  }

  h2 {
    margin-right: auto;
    font-size: 56px;
    margin-bottom: 43px;

    @include vp-1199 {
      font-size: 42px;
      font-weight: 700;
      margin-bottom: 32px;
    }

    @include vp-1199 {
      font-size: 32px;
    }
  }
}

.contacts__info {
  display: grid;
  grid-template-columns: 200px 200px;
  grid-template-rows: 1fr 1fr;
  justify-content: start;
  column-gap: 92px;
  row-gap: 16px;
  font-style: normal;
  color: $color-sapphire;
  position: relative;

  @include vp-1199 {
    grid-template-columns: 300px 300px;
    column-gap: 14px;
    row-gap: 24px;
  }

  @include vp-1199 {
    grid-template-columns: 300px 200px;
    row-gap: 24px;
  }

  @include vp-767 {
    grid-template-columns: 300px;

    & div:nth-child(2) {
      grid-row: 3/4;
    }
  }

  & div {
    align-self: start;
  }

  & b {
    display: block;
    text-decoration: none;

    @include vp-1199 {
      margin-bottom: 12px;
      text-decoration: none;
      font-size: 20px;
    }
  }

  & p,
  & a {
    @include vp-1199 {
      letter-spacing: 0;
      font-size: 18px;
    }
  }

  &::after {
    content: "";
    position: absolute;
    top: 10px;
    right: 50%;
    transform: translateX(-50%);
    width: 1px;
    height: 102px;
    background-color: $color-light-slate-grey;
    z-index: 2;

    @include vp-1199 {
      right: 48%;
    }

    @include vp-1199 {
      display: none;
    }
  }
}
